<template>
  <div class="demo-split">
        <Split v-model="split1">
            <div slot="left" class="demo-split-pane">
                <Table calss="table-style" ref="table" @on-row-click="rowClick"
                       :columns="columns1" :data="data1"
                       :row-class-name="tableRowStyle" 
                       :header-class-name="tableRowStyle"></Table>
            </div>
            <div slot="right" class="demo-split-pane">
                
            </div>
        </Split>
      <router-view v-if="true"/>
    </div>
</template>

<script>
import Info from '@/components/Info'
export default {
  name: 'Summary',
  data () {
    return {
      columns1: [
          {
            title: "Symbol",
            key: 'symbol'
          },
          {
            title: 'Name',
            key: 'name',
            width: '400px'
          },
          {
            title: 'Last Price',
            key: 'lastPrice'
          },
          {
            title: 'Change',
            key: 'change'
          },
          {
            title: '%Change',
            key: '_change'
          }
      ],
      data1:[
        {
          symbol: "DJI",
          name: "Dow Jones industrial",
          lastPrice: "26378",
          change: "371",
          _change: "+1.43%"
        },
        {
          symbol: "DJI",
          name: "Dow Jones industrial",
          lastPrice: "26378",
          change: "371",
          _change: "+1.43%"
        },
        {
          symbol: "DJI",
          name: "Dow Jones industrial",
          lastPrice: "26378",
          change: "371",
          _change: "+1.43%"
        },
        {
          symbol: "DJI",
          name: "Dow Jones industrial",
          lastPrice: "26378",
          change: "371",
          _change: "+1.43%"
        },
      ]

    }
  },
  components: {
    'data-detail': Info,
  },
  methods:{
    tableRowStyle(row, index){
        return 'table-row'
    },
    rowClick(data,index){
        this.$router.push('/Info/'+ data.symbol);
    },

    },
}
</script>


<style>
  .ivu-table .table-row td{
        background-color: #fff;
        color: #515a6e;
        font-family:"Helvetica Neue";
        font-weight: 800;
        font-size: 15px;
    }
  .table-style .ivu-table-cell{
    padding: 0px;
  }
  .table-style {
    background-color: #000000;
    color: #515a6e;
    font-family:"Helvetica Neue";
    font-weight: 800;
    font-size: 15px;
  }
</style>


